import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
/* Layout */
const Layout = () => import( /* webpackChunkName:"Layout" */ '@/layout')

const routes = [{
    path: '/',
    redirect: '/index'
  }, {
    // 登录页
    path: '/login',
    component: () => import( /* webpackChunkName:"login-index" */ '@/views/login')
  }, {
    // 注册页
    path: '/register',
    component: () => import( /* webpackChunkName:"login-index" */ '@/views/register')
  },
  {
    // 主页
    path: '/',
    component: Layout,
    children: [{
        path: 'index',
        component: () => import( /* webpackChunkName:"login-index" */ '@/views/index')
      },
      {
        path: 'mall',
        component: () => import( /* webpackChunkName:"login-index" */ '@/views/mall')
      },
      {
        path: 'mana',
        component: () => import( /*webpackChunkName:"admin"*/ '@/views/mana'),
        children: [{
            path: 'shopper',
            component: () => import( /*webpackChunkName:"admin"*/ '@/views/mana/components/shopper')
          },
          {
            path: 'scenery',
            component: () => import( /*webpackChunkName:"admin"*/ '@/views/mana/components/scenery')
          },
          {
            path: 'statistics',
            component: () => import( /*webpackChunkName:"admin"*/ '@/views/mana/components/statistics')
          },
          {
            path: 'info',
            component: () => import( /*webpackChunkName:"admin"*/ '@/views/mana/components/info')
          },
        ]
      },
      // 主页
      {
        path: 'index',
        component: () => import( /* webpackChunkName:"login-index" */ '@/views/index')
      },
      // 风景详情
      {
        path: 'scenic/details/:id',
        component: () => import( /*webpackChunkName:"user"*/ '@/views/scenicDetails')
      },
      // 商品详情
      {
        path: 'goods/details/:id',
        component: () => import( /*webpackChunkName:"user"*/ '@/views/goodsDetails')
      },
      // 搜索页
      {
        path: 'search/:type/:search',
        component: () => import( /*webpackChunkName:"user"*/ '@/views/search')
      },
      // 个人中心
      {
        path: 'userCenter',
        component: () => import( /*webpackChunkName:"userCenter"*/ '@/views/userCenter'),
        children: [{
            path: 'order',
            component: () => import( /*webpackChunkName:"userCenter"*/ '@/views/userCenter/components/order')
          },
          {
            path: 'msg',
            component: () => import( /*webpackChunkName:"userCenter"*/ '@/views/userCenter/components/usermsg')
          },
          {
            path: 'goods',
            component: () => import( /*webpackChunkName:"userCenter"*/ '@/views/userCenter/components/goods')
          },
          {
            path: 'order-shop',
            component: () => import( /*webpackChunkName:"userCenter"*/ '@/views/userCenter/components/orderShop')
          },
          {
            path: 'statistics',
            component: () => import( /*webpackChunkName:"userCenter"*/ '@/views/userCenter/components/statistics')
          },
        ]
      },
    ]
  }
]
const router = new Router({
  routes
})
// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
  // to 将要访问的路径
  // from 代表从哪个路径跳转来的
  // next 为函数，表示放行
  // next() 放行 next('/login') 强制跳转
  // 路由检测白名单
  if (to.path === '/login' || to.path === '/register') return next()
  const userstr = window.sessionStorage.user
  if (to.path.includes('mana')) {
    Vue.prototype.isadmin = true
    // 若未检测到userstr则强制跳转到登录页面
    if (userstr) {
      var role = JSON.parse(userstr).role_id ? JSON.parse(userstr).role_id : JSON.parse(userstr).role
    }
    if (!userstr || role.role_Id !== 1) {
      return next('/login')
    }
    next()
  } else {
    if (to.path === '/login') {
      Vue.prototype.isadmin = false
    }
  }
  if (to.path.includes('userCenter')) {
    Vue.prototype.isadmin = false
    // 若未检测到userstr则强制跳转到登录页面
    if (!userstr) {
      return next('/login')
    }
  }
  next()
})

export default router
